import React, { useContext } from "react";
import { FormControl, InputLabel, Select, MenuItem } from "@material-ui/core";
import { useStyles } from "../../styles";
import globalContext from "../../context/global/globalContext";
import { SET_CURRENT_DURATION } from "../../context/types";

const DurationsSelect = (props) => {
  const classes = useStyles();
  const { durations, currentDuration, dispatch } = useContext(globalContext);

  return (
    <FormControl
      variant="outlined"
      disabled={props.disabled}
      style={props.style}
    >
      <InputLabel id="DurationsSelect">Rejsel√¶ngde</InputLabel>
      <Select
        id="DurationsSelect"
        value={currentDuration}
        label="DurationsSelect"
        onChange={(e) => {
          dispatch({
            type: SET_CURRENT_DURATION,
            payload: e.target.value,
          });
        }}
      >
        <MenuItem disabled selected value>
          -- V√¶lg rejsel√¶ngde --
        </MenuItem>
        {durations?.map((duration, key) => (
          <MenuItem key={key} value={duration.days}>
            {duration.days} dage
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};

export default DurationsSelect;
